import { element } from 'protractor';
import { StorageService } from './../../../services/storage.service';
import { ActivatedRoute } from '@angular/router';
import { LoaderService } from 'src/app/services/loader.service';
import { ApiPredictorService } from './../../../services/api-predictor.service';
import { ApiGeneralService } from 'src/app/services/api-general.service';
import { HeaderTitleService } from 'src/app/services/header-title.service';
import { Component, OnInit } from '@angular/core';
import { environment } from 'src/environments/environment.prod';

@Component({
  selector: 'app-counselling-type',
  templateUrl: './counselling-type.page.html',
  styleUrls: ['./counselling-type.page.scss'],
})
export class CounsellingTypePage implements OnInit {
  examId = 0;
  arrReviews = [];
  webAssetsPathReviews = "";
  slideOpts = { slidesPerView: 1.2 }

  arrSlider = [];
  slideBannerOpts = { slidesPerView: 1, autoplay: true }
  AssetsPathSlider = environment.webAssetsPath + "slider-images";

  arrCounselling = [];
  arrUserData: any;
  arrSavedPredictor = [];

  constructor(
    private headerTitleService: HeaderTitleService,
    private apiGeneral: ApiGeneralService,
    private apiPredictorService: ApiPredictorService,
    private loader: LoaderService,
    private route: ActivatedRoute,
    private storage: StorageService
  ) {
    this.headerTitleService.isBack = true;
    this.headerTitleService.title = "NEET College Predictor";
    this.examId = this.route.snapshot.params['examId'];
  }
  ionViewWillEnter() {
    this.getUserData(); // getSavedPredictor => getCousellingType()
  }
  ngOnInit() {
    this.getSlider();
    this.getReview();
  }
  doRefresh(event) {
    this.getSlider();
    this.getReview();
    setTimeout(() => { event.target.complete() }, 2000);
  }
  getUserData() {
    this.loader.presentLoading();
    this.storage.getObject("userdata").then(resp => {
      this.arrUserData = resp;
      this.getSavedPredictor();
    })
  }
  getCousellingType() {
    let postData = { "IsActive": true };
    this.apiPredictorService.getCousellingType(JSON.stringify(postData)).then(resp => {
      this.arrCounselling = resp.map(t1 => ({ ...t1, ...this.arrSavedPredictor.find(t2 => t2.counsellingId === t1.counsellingId) }));
      console.log(this.arrCounselling);
      this.loader.loadingDismiss();
    }).catch(ex => {
      console.log("getCousellingType()=> " + ex);
    })
  }
  getSavedPredictor() {
    let postdata = { "ExamID": this.examId, "AppLoginId": this.arrUserData["appLoginID"], "IntakeYear": "2020" }
    this.apiPredictorService.getSavedPredictor(JSON.stringify(postdata)).then(resp => {
      if (resp) {
        this.arrSavedPredictor = resp;
        this.getCousellingType();
        console.log(this.arrCounselling);
      }
    }).catch(ex => {
      console.log("getSavedPredictor" + ex);
      this.loader.loadingDismiss();
    })
  }

  //#region Slider
  getSlider() {
    let postData = { "IsMobile": true, "SliderLocation": "App-Predictor" };
    this.apiGeneral.getSlider(JSON.stringify(postData)).then(resp => {
      this.arrSlider = resp;
    }).catch(ex => {
      console.log("getSlider()=> " + ex);
    })
  }
  //#endregion

  //#region Get Reviews
  getReview() {
    let postData = { "Category": "Predictor", "IsActive": true }
    this.apiGeneral.getReviews(JSON.stringify(postData)).then(resp => {
      this.arrReviews = resp;
      this.webAssetsPathReviews = environment.webAssetsPath + "mobility-app/student-review";
    })
      .catch(err => {
        console.log("getReview: " + err);
      })
  }
  //#endregion

}
